body {
  font-family: sans-serif;
  color: white;
  background-color: #0a090b;
  margin: 0;
  padding: 1rem;
}

#root {
  display: flex;
  flex-direction: column;
  border: 0.2rem solid #1b191d;
  border-radius: 0.2rem;
}

/*
*   Navbar
*/
nav {
  flex: 1;
  background-color: #1b191d;
}
nav ul {
  padding: 1rem;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.menu-item {
  padding: 0.5rem 1rem;
  margin: 0 0.5rem;
  transition: ease 0.3s;
  border-radius: 0.1rem;
}
.menu-item:hover {
  background-color: #8132ad;
}

/*
*   Introduction
*/

#introduction {
  flex: 1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}
#icon-div {
  display: flex;
  justify-content: flex-end;
}
.icon,
svg {
  flex: 1 1 90%;
  max-width: 5rem;
  min-width: 3rem;
  max-height: 5rem;
  align-self: center;
}
#logo {
  flex: 1;
  max-width: 22rem;
  min-width: 3rem;
  max-height: 15rem;
  align-self: center;
}
#info {
  margin: 0 0 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#info p,
h1 {
  margin: 0.5rem 0;
  padding: 0;
}

/*
*   Skills
*/

#skills {
  flex: 1;
  padding: 3rem 5rem 3rem 5rem;
}

#skill-grid {
  list-style: none;
  margin: 0;
  padding: 2rem 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  border-radius: 0.4rem;
  background-color: #1b191d;
}

.skill {
  padding: 1rem;
  flex: 1 1 20%;
  max-width: 18%;
  min-width: 15%;
  margin: 1rem;
  border: 2px solid white;
  border-radius: 0.4rem;
  display: flex;
  justify-content: center;
  background-color: #0a090b;
  transition: 0.3s ease-in-out;
}

.skill:hover {
  transform: scale(1.1);
}

/*
*   Experience
*/

#experience {
  flex: 1;
  display: flex;
  justify-content: center;
  border: 2px solid blue;
  padding: 1rem;
}
#experience p {
  width: 50%;
  margin: 0;
  padding: 0;
}
/*
*   Contact Form
*/

#contact {
  flex: 1;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

/*#contact h2 {
}*/

#form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

#form h2 {
  align-self: center;
}

#icon-div2 {
  display: flex;
  justify-content: center;
}

input,
textarea {
  color: white;
  background-color: #1b191d;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
  resize: none;
  transition: ease 0.3s;
}

input:hover,
textarea:hover {
  background-color: #141316;
}

input:focus,
textarea:focus {
  outline: none;
  background-color: #141316;
}

button {
  align-self: center;
  color: white;
  background-color: #8132ad;
  border: 2px solid #1b191d;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
  padding: 0.6rem 3rem;
  transition: ease 0.3s;
}

button:hover {
  background-color: #53206f;
  transform: scale(1.1);
}
/*
*   Footer
*/
footer {
  flex: 1;
  display: flex;
  justify-content: space-between;
  background-color: #1b191d;
}

footer h3 {
  margin-left: 1rem;
  align-self: center;
}

#social {
  margin: 0;
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: flex-end;
}

.network {
  padding: 1rem;
  margin: 0;
  transition: ease 0.5s;
}

#social li:first-child {
}
#social li:nth-child(2) {
  background-color: #ffffff;
}
#social li:nth-child(3) {
}

#social li:first-child:hover {
  background-color: #8132ad;
  transform: scale(1.1);
}

#social li:nth-child(2):hover {
  background-color: #8f91a2;
  transform: scale(1.1);
}
#social li:nth-child(3):hover {
  background-color: #8132ad;
  transform: scale(1.1);
}
